import react,{ Component } from 'react';
import {
    ManagerListWrapper,
    ManageTable,
    ManagerItemTR
}from '../style'
class ManagerOrderList extends Component{
    render() {
        const order={};
        return (
            <ManagerListWrapper>
            {/* style="margin-left:150px;width:1000px;" */}
                <ManageTable class="table table-hover" >
                <caption>订单列表</caption>
                <thead>
                    <tr>
                        <th>订单号</th>
                        <th>书籍名称</th>
                        <th>订单用户</th>
                        <th>订单地址</th>
                        <th>订单时间</th>
                    </tr>
                </thead>
                <tbody>
                
                    <ManagerItemTR>
                    <tr>
                        <td>${order.oid }</td>
                        <td>${order.goodName }</td>
                        <td>${order.userid }</td>
                        <td>${order.detailAddress }</td>
                        <td>${order.date }</td>
                    </tr>
                    
                    </ManagerItemTR>
        
                </tbody>
                </ManageTable>
        
        </ManagerListWrapper>
         
        )
    }
}
export default ManagerOrderList;  