import styled from 'styled-components';
export const ManagerPageWrapper=styled.div`
    margin-top:20px;
    margin-left:10px;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-wrap:wrap;

`
export const ManagerSearchFormWrapper=styled.div`
    display:block;
    width:100%;
    margin:0 auto;
    height:200px;

`

export const ManagerListWrapper=styled.div`
    width:100%;
    border-top:1px solid #e7e7e7;

`
//ManagerSearchForm start
export const FromGroup=styled.div`
    display:flex;
    justify-content: center;
    align-items: center;
    flex-wrap:wrap;
    label{
        color:rgb(51, 51, 51);
        float:left;
        display:block;
        font-size:14px;
        font-weight:700;
        padding:10px;
    }
    input{
        
            border:1px solid rgb(204, 204, 204);
            border-radius:4px;
            box-shadow:rgba(0, 0, 0, 0.075) 0px 1px 1px 0px inset;
            color:rgb(85, 85, 85);
            height:34px;
            line-height:20px;
            width:300px;
        
    }
    button{
        background-color:rgb(51, 122, 183);
        border-radius:4px;
        border:1px solid rgb(46, 109, 164);
        color:rgb(255, 255, 255);
        margin-top:10px;
        margin-left:50px;
        cursor:pointer;
        font-size:14px;
        padding:5px 12px;

    }
`;
//ManagerSearchForm end
//Table start
export const ManageTable=styled.table`

    width:80%;
    margin-left:150px;
    display:table;
   
    text-align:left;


    caption{
        color:rgb(119, 119, 119);
        font-size:14px;
        padding:8px;
        text-align:left;
        margin:10px;
    }
    
    tr{
        font-weight:700;
        font-size:14px;
        margin:10px;
        border-bottom:1px solid #e7e7e7;
        padding-left:5px;
        th{
            padding-left:20px;
        }
        
        
    }
    thead{
      
        height:30px;
     
        line-height:30px;
        
    }
    


`
export const ManagerItemTR=styled.tr`
   height:30px;
   line-height:30px;
   border-top:1px solid  #e7e7e7;
   margin:5px;
   td{
       padding:5px;
   }



`
//Table end