import styled from 'styled-components';
export const HeaderWrapper=styled.div`
    position:relative;
    a{
        text-decoration:none;
        color:rgb(119, 119, 119);
        font-size:14px;
        line-height:32px;
    }
    .brand{
        font-size:18px;
    }
    .button{
        //  float:left;
    }
`
export const Nav=styled.div`
    padding:8px;
    font-size:14px;
    background-color:rgb(248, 248, 248);
    height:40px;
    width:98.7%;
    border-bottom:1px solid  rgb(231, 231, 231);
    &.footBar{
        position:fixed;
        
        bottom:0;
        color:rgb(51, 51, 51);
        font-size:14px;
        p{
            
            line-height:50px;
            text-align:center;
        }
     
    }

`
export const NavBar=styled.div`
    width:200px;
    float:left;
   

`
