import react,{ Component } from 'react';
import LeftSide from '../PersonPage/components/LeftSide';

import {
    UpdateInfoWrapper,
    MainSection,
    UpdateTitle,
    UpdateForm,
    Button
}from './style'
class UpdateInfo extends Component{
    render(){
        const phone={};
        return(
            <UpdateInfoWrapper>
                <LeftSide/>
                <MainSection>
                    <UpdateTitle class="data">
                        <span>基本资料</span>
                    </UpdateTitle>
                    <UpdateForm  id="stuForm" >
                    
                        <div class="data1">
                            <img src="http://img7x1.ddimg.cn/imghead/15/9/9921910134921-1_o.png?=5792"/>
                            <span >$</span>
                        </div>
                        <div class="form-group">
                            <label  for="name">昵称</label>     
                            <input id="username"  name="name" className="name" />
                                
                        </div>
                    
                        <div class="form-group">
                            <label for="name">性别</label>  
                            <div class="radio" >
                                <label class="radio-inline"><input name="sex" type="radio" value="男" />男</label>
                            </div>
                            <div class="radio">
                                <label class="radio"><input name="sex" type="radio" value="女" />女</label>
                            </div>
                        </div>
{/*  style="margin-top:10px; */}
                        <div class="form-group">
                            <label for="name">地址</label>  
                            <div id="target">
                                {/*  style="width:20%;display:inline-block;margin-right:10px;" */}
                                <select class="form-control" name="province">
                                
                                </select>
                                {/*  style="width:20%;display:inline-block;margin-right:10px;" */}
                            <select class="form-control" name="city"></select>
                            {/* style="width:20%;display:inline-block;margin-right:10px;" */}
                                <select class="form-control" name="area" ></select>
                            </div>
                        </div>
                    <Button class="btn btn-primary">提交</Button>
                    </UpdateForm> 
                </MainSection>

            </UpdateInfoWrapper>
        )
    }
}
export default UpdateInfo;