import react,{Component}from 'react';
import {
    HotSaleWrapper,
    HotTitle,
    Rank,
    ImgHidden,
    HotList
}from '../style'
class HotBook extends Component{
    
    render(){
        const { hotList }=this.props;
        return(
            <HotSaleWrapper className="hotSaled">
                <HotTitle className="hotTitle">新书热卖榜</HotTitle>
                <div className="listMenu">
                    <ul className="lg-list">
                    {/* <!-- 通过访问servlet为index的内容，返回书本销量大于1000的书籍10本循环展示 --> */}
                 
                        <HotList className="lg-item" >
                            <Rank className="rank"> {hotList.get("rank")}</Rank>
                            <div className="show1">$name(book.writer)</div>
                            <ImgHidden className="hidden">
                             
                                <img src="" className="small" alt="" />
                                <p className="newName" >name(writer)</p>
                                <span className="newPrice">￥price</span>       
                                <span className="oldPrice">￥$oldPrice</span>    
                            </ImgHidden>               
                        </HotList>
                    

                     </ul>
       
            </div>      
        </HotSaleWrapper>
        )
    }
}
export default HotBook;