import react,{Component} from 'react';
import Button from '../../../components/Button/index';
import {
    ManagerListWrapper,
    ManageTable,
    ManagerItemTR
}from '../style'
class ManagerBookList extends Component{
    render(){
        const book={}
        return(

                <ManagerListWrapper>
                    {/* style="margin-left:150px;width:1000px;" */}
                        <ManageTable class="table table-hover" >
                            <caption>书籍列表</caption>
                            <thead>
                                <tr>
                                    <th>书籍编号</th>
                                    <th>书籍名称</th>
                                    <th>作者</th>
                                    <th>价格</th>
                                    <th>出版社</th>
                                    <th>销售数量</th>
                                </tr>
                            </thead>
                            <tbody>
                        
                            <ManagerItemTR>
                                <td>${book.bid }</td>
                                <td>${book.name }</td>
                                <td>${book.writer }</td>
                                <td>${book.price }</td>
                                <td>${book.publisher }</td>
                                
                                <td>
                                    <Button className="primary four addBook">添加书籍</Button>
                                    <Button className="info four delete">删除书籍</Button>
                                    <Button className="success four update" >修改书籍</Button>
                                </td>
                            
                            </ManagerItemTR>
                
                            </tbody>
                        </ManageTable>
                
                </ManagerListWrapper>
                 

        )
    }
}
export default ManagerBookList;