import React,{Component} from 'react';
import { BookBlock , BookCaption } from '../../IndexBookPage/style'
import {
    GusLikeWrapper,
    GusTitle,
  
}from '../style'
class GusLike extends  Component{
    render(){
        const {book}=this.props;
        return(
            <GusLikeWrapper>
                <GusTitle class="gusTitle"> 猜你喜欢</GusTitle>
                <div class="col-sm-6 col-md-4">
                    <BookBlock class="thumbnail">
                        <img src="${book.src}" 
                        alt="通用的占位符缩略图"/>
                        <BookCaption class="caption">
                            {/* <p class="bookprice">￥ $book.price <span class="oldPrice">￥${book.oldPrice}</span></p>
                            <a href="#" class="bookName">${book.name}</a>
                            <span class="writer">[${book.writer },${book.publisher }]</span> */}
                            
                        </BookCaption>
                    </BookBlock>
	         
	            </div>
	
            </GusLikeWrapper>
        )
    }
}
export default GusLike;