import react,{ Component } from 'react';
import {
    ManagerListWrapper,
    ManageTable,
    ManagerItemTR
}from '../style'
class ManagerUserList extends Component{
    render() {
        const user={};
        return (
            <ManagerListWrapper>
            {/* style="margin-left:150px;width:1000px;" */}
                <ManageTable class="table table-hover" >
                <caption>用户列表</caption>
                <thead>
                    <tr>
                        <th>账号</th>
                        <th>昵称</th>
                        <th>性别</th>
                        <th>地址</th>
                        
                    </tr>
                </thead>
                    <tbody>
                
                    <ManagerItemTR>
                        <tr>
                            <td>${user.name }</td>
                            <td>${user.phone }</td>
                            <td>${user.sex }</td>
                            <td>${user.province }${user.city }</td>
                        
                            
                        
                        
                        </tr>
                    
                    </ManagerItemTR>
        
                    </tbody>
                </ManageTable>
        
        </ManagerListWrapper>
        )
    }
}
export default ManagerUserList;  