import React,{Component} from 'react';

import {
    BookMainWrapper,
    BookMainDetail,
    DetailTitle,
    BookComment,
    CommentBlock
}from '../style'
class BookMain extends  Component{
   
    render(){
        const {comment,book}=this.props;
        return(
            <BookMainWrapper>
                <DetailTitle class="detailTil">
                    <span class="bookDeatil til">书本详情</span>
                    <span class="comment til ">评论</span>
                </DetailTitle>  
               <BookMainDetail>
               
                    <div class="goodsMain">
                
                        <span class="goodname">产品名称:&nbsp;${book.name }</span>
                        <span class="mainTil">产品参数:</span>
                        <span class="first">开本：32开</span>
                        <span>纸张：胶版纸</span>
                        <span>包装：精装</span>
                        <span>是否套装：否</span>
                        <span>所属分类:图书&gt;历史&gt;世界史&gt;其他</span>
                 
                    </div>
               </BookMainDetail>
               <BookComment>
                <CommentBlock class="comblock">
                        <p class="content">评价：&nbsp;${comment.content }</p>
                        <span class="uName">${comment.userid }</span>
                    </CommentBlock>
               </BookComment>
            </BookMainWrapper>
        )
    }
}
export default BookMain;