import React,{ Component } from 'react';
import { Link } from 'react-router-dom';
import  {
    BookItemWrapper,
    BookItemTitle,
    BookBlock,
    BookCaption
} from '../style';
class BookItem extends  Component{
    
    render(){
        const { bookList }=this.props;
        return(
          
                <BookItemWrapper>
                    <BookItemTitle class="newBookTitle">最新上架 </BookItemTitle>
                    <div class="row">
                        <Link to='/book'>
                            <BookBlock/>
                        </Link>
                        {
                            bookList.map((item)=>{
                                <BookBlock class="col-sm-6 col-md-3 block" >
                                    <Link to={{
                                            pathname:`/book`,
                                            state:item
                                            }} >
                                    <div class="thumbnail"  >
                                        <img src="" 
                                        alt="通用的占位符缩略图" onclick=""/>
                                        <BookCaption class="caption">
                                            <p class="bookprice">￥ $price<span class="oldPrice">￥$oldprice</span></p>
                                            {/* // eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                            <a href="#" class="bookName">$bookname</a>
                                            <span class="writer">[writer,publisher]</span>
                                            
                                        </BookCaption>
                                    </div>
                                    </Link>
                                </BookBlock>
                            })
                        }
                     
                    </div>
                </BookItemWrapper>
   
            
        )
    }
}
export default BookItem;