import React,{Component} from 'react';
import NewBook from './components/New';
import Carousel from './components/Carousel';
import PreSaleNew from './components/PresaleNew';
import Hot from './components/Hot'
import BookItem  from './components/BookItem'
import { connect } from 'react-redux';
import { actionCreators } from './store/index';
import {
    IndexBookWrapper,
    IndexLeftWrapper,
    IndexRightWrapper
}from './style';
class IndexBook extends  Component{
    componentWillMount(){
        this.props.getIndexList();
    }
    render(){
        const { imgList , hotList ,bookList }=this.props;
        return(
            <IndexBookWrapper>
              <IndexLeftWrapper>
                <NewBook/>
                <Hot hotList={ hotList }/>
              </IndexLeftWrapper>
                
              <IndexRightWrapper>
                    <Carousel imgList={ imgList }/>
                    <PreSaleNew/>
                    
                    <section>
                        
                        <BookItem bookList={bookList}/>
                    </section>
              </IndexRightWrapper>
            </IndexBookWrapper>
        )
    }
}
const mapState = (state) => ({
	imgList: state.getIn(['index', 'imgList']),
    hotList:state.getIn(["index","hotList"]),
    bookList:state.getIn(["index","bookList"]),
})

const mapDispatch = (dispatch) => ({
	getIndexList(){
		dispatch(actionCreators.getIndexList());
	}
})
export default connect(mapState,mapDispatch)(IndexBook);