import React,{Component}from 'react';
import { Redirect , Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { actionCreators } from '../store';
import Button from '../Button/index';
import {
    HeaderWrapper,
    Nav,
    NavBar
}from './style';
class Header extends Component{
    
    
    render(){
       
        const {loginState,loginOut}=this.props;
        console.log("loginState"+loginState);
        return(
            <HeaderWrapper>
                <Nav>
                      
                        <NavBar>
                            <Link className="brand" to="/index">网上书店</Link>
                        </NavBar>               
                        <div>
                            {
                                !loginState?(
                                    <div className="button">
                                      <Button  className="default" href="login.jsp">注册</Button>
                                      <Link to="/login">
                                            <Button className="primary" href="register.jsp">登录</Button>
                                      </Link>
                                      
                                    </div>
                                    
                                ):(
                                    <div> 
                                        <Button className="default four" onClick={loginOut}>退出登录</Button>
                                        <Link to="/person">
                                            <Button  className="personal primary">hi,</Button>
                                        </Link>
                                        <Link to="/managerBook">
                                            <Button className="success four" href="AllBook">管理书籍  </Button>
                                        </Link>
                                        <Link to="/managerOrder">
                                            <Button  className="info four" href="ShowOrder">  管理订单</Button>
                                        </Link>
                                        
                                        <Link to="/managerUser">
                                            <Button  className="warning four" href="AllUser"> 管理用户 </Button>
                                        </Link>
                                       
                                        
                                       
                                    </div>
                                    
                                )
                            }
                            
                            
                        </div> 

                </Nav>
            </HeaderWrapper>
        )
    }
}
const mapState = (state) => ({
	loginState: state.getIn(['common', 'login'])
})

const mapDispatch = (dispatch) => ({
	login(accountElem, passwordElem){
		dispatch(actionCreators.login(accountElem.value, passwordElem.value))
	},
    loginOut(){
        dispatch(actionCreators.logout());
    }
})

export default connect(mapState, mapDispatch)(Header);