import React,{Component} from 'react';
import {
    RecentOrderWrapper,
    OrderTitle,
    OrderItem
}from '../style'
class  RecentOrder extends  Component{
    render(){
        const order={}
        return(
            <RecentOrderWrapper>
                <OrderTitle class="goodsTitle"> 最近订单</OrderTitle>
                <div class="col">
                    <ul class="lg-list">
                        <OrderItem class="lg-item">
                            <img src="${order.goodPic }"/>
                            <p class="detail">送达：${order.detailAddress }&nbsp;&nbsp;&nbsp;联系方式:${order.userid }</p>
                            <a class="btn btn-info assess" onclick="document.getElementById('${order.oid}').submit()">评价</a>
                        </OrderItem>
                    </ul> 
                </div>
            </RecentOrderWrapper>
        )
    }
}
export default RecentOrder;